import React, { useState, useEffect } from "react"
import { Section, Button, ButtonLink, Empty, Loading } from "components"
import { Form, Tag } from "antd"

import { randomColor, client, renderError, mobile } from "helpers"
import { coolColors } from "dummy"
import SelectInput from "components/forms/SelectInput"
import { useMutation } from "react-apollo"
import { mutateUpdateSkills, mutateDeleteSkills, MUTATE_FIND_SKILLS } from "queries/profile"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { Formik } from "formik"
import { SubmitButton } from "formik-antd"

function Skills({ onEditMode, onSkills, fetchUser, loadingSkills, unauthorizedUser, i18n }) {
	const { editMode, setEditMode } = onEditMode
	const { skills = [] } = onSkills
	const [selectedItems, setSelectedItems] = useState(skills)
	const [skillsData, setSkillsData] = useState([])

	const [updateSkills] = useMutation(mutateUpdateSkills, {
		client: client.profileClient,
		onCompleted: data => {
			fetchUser()
			setEditMode(false)
		},
		onError: err => renderError(err)
	})

	const [deleteSkills] = useMutation(mutateDeleteSkills, {
		client: client.profileClient,
		onError: err => renderError(err)
	})

	const [fetchSkills, { loading: loadingSkillsData }] = useMutation(MUTATE_FIND_SKILLS, {
		client: client.profileClient,
		onCompleted: data => {
			data = data.findSkills || []
			const mapped = data.map(item => ({ value: item.title, label: item.title }))
			setSkillsData(mapped)
		}
	})

	const handleSaveSkills = (values, { setSubmitting }) => {
		const deletedItems = values.existing.filter(({ title }) => !(values.skills || []).includes(title))
		values = {
			...values,
			skills: (values.skills || []).map(item => {
				const isThere = values.existing.find(({ title }) => title === item)
				return { pvid: isThere ? isThere.pvid : "", title: item }
			})
		}
		deleteSkills({ variables: { pvidList: deletedItems.map(item => item.pvid) } })
		updateSkills({ variables: { list: values.skills } }).finally(() => setSubmitting(false))
	}

	useEffect(() => {
		fetchSkills({ variables: { filter: "x" } })
		setSelectedItems(skills)
	}, [fetchSkills, skills])

	// prettier-ignore
	if (loadingSkills) return <Loading />

	return (
		<Section paddingHorizontal={0} noPadding={mobile} marginBottom={0}>
			{editMode === "skills" && !unauthorizedUser ? (
				<Formik
					initialValues={{
						skills: skills.map(({ title }) => title),
						existing: skills
					}}
					onSubmit={handleSaveSkills}
				>
					{({ handleSubmit }) => (
						<Form layout="vertical" onSubmit={handleSubmit}>
							<SelectInput
								name="skills"
								mode="tags"
								placeholder={i18n._(t`Your skills`)}
								options={skillsData}
								loading={loadingSkillsData}
								label={<Trans>Enter your skills</Trans>}
								filterOption={(input, { props }) => {
									const { children } = props
									const option = (children.props.children || "").toLowerCase()
									return option.indexOf(input.toLowerCase()) !== -1
								}}
							/>
							<SubmitButton type="primary">
								<Trans>Save changes</Trans>
							</SubmitButton>{" "}
							&nbsp;
							<ButtonLink size="small" onClick={() => setEditMode(false)}>
								<Trans>Cancel</Trans>
							</ButtonLink>
						</Form>
					)}
				</Formik>
			) : (
				<>
					{unauthorizedUser ? (
						<Section paddingHorizontal={0} marginBottom={0} centered>
							{skills.length === 0 ? (
								<Empty description={<Trans>This tutor didn't specify any skills yet</Trans>} />
							) : (
								skills.map(({ pvid, title }) => {
									const color = randomColor(coolColors)
									return (
										<Tag key={pvid} color={color} style={{ marginBottom: ".7em" }}>
											{title}
										</Tag>
									)
								})
							)}
						</Section>
					) : (
						<>
							{loadingSkills ? (
								<Loading />
							) : skills.length === 0 ? (
								<Section paddingHorizontal={0} marginBottom={0} centered>
									<Empty description={<Trans>You didn't specify any skills yet</Trans>} />
									<Button type="primary" icon="plus" onClick={() => setEditMode("skills")}>
										&nbsp; <Trans>Add one</Trans>
									</Button>
								</Section>
							) : (
								skills.map(({ pvid, title }) => {
									const color = randomColor(coolColors)
									return (
										<Tag key={pvid} color={color} style={{ marginBottom: ".7em" }}>
											{title}
										</Tag>
									)
								})
							)}
						</>
					)}
				</>
			)}
		</Section>
	)
}

export default withI18n()(Skills)
